import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Quiz from './Quiz';
import Result from './Result';
import ResultadoInapto from './ResultadoInapto';
import App from '../App';

import DicionarioArvores from '../api/DicionarioArvores.js'
import perguntasQuiz from '../api/perguntasQuiz.js';

class PaginaInicial extends Component {
  constructor(props) {
    super(props);

    this.state = {
      counter: 0,
      questionId: 1,
      question: '',
      questionInfo: '',
      answerOptions: [],
      answer: '',
      arrayRespostas: [],
      answerType: '',
      result: '',
      grupo: '',
      cont: 0,
      perguntasPuladas: 0
    };

    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
    this.setUserAnswer = this.setUserAnswer.bind(this);
    this.resetUserAnswer = this.resetUserAnswer.bind(this);
    this.handleBtnVoltar = this.handleBtnVoltar.bind(this);
    this.renderResult = this.renderResult.bind(this);
  }

  componentWillMount() {
    this.setState({
      question: perguntasQuiz[0].question,
      questionInfo: perguntasQuiz[0].questionInfo,
      answerOptions: perguntasQuiz[0].answers
    });
  }

  handleAnswerSelected(event) {
    this.setUserAnswer(event.currentTarget.value);
    if (event.currentTarget.value === "inapto"){
      this.setState({ result: "inapto"});
    } else {
      if (this.state.questionId < perguntasQuiz.length && event.currentTarget.value !== "fiacaoN") {
        if (event.currentTarget.value === "tipoConvencional") {
          this.setState({ perguntasPuladas: 2});
          setTimeout(() => this.setSkipQuestion(2), 300);
        }
        else {
          setTimeout(() => this.setNextQuestion(), 300);
        }
      } else {
        if (event.currentTarget.value === "fiacaoN") {
          this.setState({ perguntasPuladas: 3});
          setTimeout(() => this.setSkipQuestion(3), 300);
        }
        else {
          this.setState({
            cont: this.state.cont + 1
          });
          setTimeout(() => this.getResults(), 300);
        }
      }
    }
  }

  handleBtnVoltar() {
    if (this.state.questionId > 1) {
      if (this.state.questionId === (perguntasQuiz.length-1) ) {
        if (this.state.perguntasPuladas === 2) {
          setTimeout(() => this.setVoltarQuestion(2), 300);
        }
        else if (this.state.perguntasPuladas === 3) {
          setTimeout(() => this.setVoltarQuestion(3), 300);
        }
        else {
          setTimeout(() => this.setVoltarQuestion(1), 300);
        }
      }
      else {
        setTimeout(() => this.setVoltarQuestion(1), 300);
      }
    }
    if (this.state.questionId === 1) {
      setTimeout(() => this.voltaPaginaIncial(), 300);
    }

  }

  setUserAnswer(answer) {
    this.setState({
        // answersCount: updatedAnswersCount,
        answer: answer,
        arrayRespostas : [...this.state.arrayRespostas, answer]
    });
  }

  resetUserAnswer(){
    let array = [...this.state.arrayRespostas];
    let index = array.indexOf('');
    array.splice(index, 1);
    this.setState({arrayRespostas : array});
  }

  setNextQuestion() {
    const counter = this.state.counter + 1;
    const questionId = this.state.questionId + 1;
    this.setState({
        counter: counter,
        questionId: questionId,
        question: perguntasQuiz[counter].question,
        questionInfo: perguntasQuiz[counter].questionInfo,
        answerOptions: perguntasQuiz[counter].answers,
        answer: ''
    });
  }

  setVoltarQuestion(qtdVolta) {
    const counter = this.state.counter - qtdVolta;
    const questionId = this.state.questionId - qtdVolta;
    this.resetUserAnswer();
    this.setState({
        counter: counter,
        questionId: questionId,
        question: perguntasQuiz[counter].question,
        questionInfo: perguntasQuiz[counter].questionInfo,
        answerOptions: perguntasQuiz[counter].answers,
        answer: ''
    });
  }

  setSkipQuestion(qtdPula) {
    const counter = this.state.counter + qtdPula;
    const questionId = this.state.questionId + qtdPula;
    this.setState({
        counter: counter,
        questionId: questionId,
        question: perguntasQuiz[counter].question,
        questionInfo: perguntasQuiz[counter].questionInfo,
        answerOptions: perguntasQuiz[counter].answers,
        answer: ''
    });
  }

  getResults() {
    const respostas = this.state.arrayRespostas;
    let caminho, col, fiacao, tipoFiacao, isolamento, recuo, viario;
    let col1=0, col2=0;

    //Define valores pra comparação.
    for (let i=0; i<respostas.length; i++){
      let value = respostas[i];
      if (value==="col2"){
        col2++;
      } else if (value==="col1"){
        col1++;
      } else {
        if (value==="fiacaoN"){
          fiacao = value;
        } else if (value==="fiacaoS"){
            fiacao = value;
        } else if (value==="tipoCompacta") {
            tipoFiacao = value;
        } else if (value==="tipoConvencional") {
            tipoFiacao = value;
        } else if (value==="isolamentoS"){
            isolamento = value;
        } else if (value==="isolamentoN"){
            isolamento = value;
        } else if (value==="recuoS"){
            recuo = value;
        } else if (value==="recuoN"){
            recuo = value;
        } else if (value==="viarioP"){
            viario = value;
        }  else if (value==="viarioO"){
            viario = value;
        }
      }
    }

    //Define qual coluna
    if (col1 > 0){
      this.setState({ result: "P",
      grupo: DicionarioArvores.grupoP});
    } else {
        if (col2 > 0){
          col = "c2";
        } else {
          col = "c3";
        }
    }

    //Define qual o caminho
    if (fiacao==="fiacaoN" || (fiacao==="fiacaoS" && (tipoFiacao==="tipoCompacta" && isolamento==="isolamentoS"))){
      if (recuo==="recuoS"){
        if (viario==="viarioO"){
          caminho = "caminho1";
        }else if (viario==="viarioP"){
          caminho = "caminho2";
        }
      } else if (recuo==="recuoN"){
        if (viario==="viarioO"){
          caminho = "caminho3";
        }else if (viario==="viarioP"){
          caminho = "caminho4";
        }
      }
    } else if(fiacao==="fiacaoS" && ((tipoFiacao==="tipoCompacta" && isolamento==="isolamentoN") || tipoFiacao==="tipoConvencional")){
      if (recuo==="recuoS"){
        if (viario==="viarioO"){
          caminho = "caminho5";
        }else if (viario==="viarioP"){
          caminho = "caminho6";
        }
      } else if (recuo==="recuoN"){
        if (viario==="viarioO"){
          caminho = "caminho7";
        }else if (viario==="viarioP"){
          caminho = "caminho8";
        }
      }
    }
    //Junta Caminho e coluna e retorna qual grupo de arvore.
    if (respostas[0]==='larg2'){
        if (col === "c3"){
          if (caminho === "caminho1"||caminho === "caminho5") {
            this.setState({ result: "G1",
                            grupo: DicionarioArvores.grupoG1});
          } else if (caminho ===  "caminho2"||caminho === "caminho6") {
            this.setState({ result: "G2",
                            grupo: DicionarioArvores.grupoG2});
          } else if (caminho === "caminho3"||caminho === "caminho7") {
            this.setState({ result: "G1",
                            grupo: DicionarioArvores.grupoG1});
          } else if (caminho === "caminho4"||caminho === "caminho8") {
            this.setState({ result: "G3",
                            grupo: DicionarioArvores.grupoG3});
          }
        } else if (col === "c2"){
          if (caminho === "caminho1") {
            this.setState({ result: "M1",
                            grupo: DicionarioArvores.grupoM1});
          } else if (caminho === "caminho2") {
            this.setState({ result: "M2",
                            grupo: DicionarioArvores.grupoM2});
          } else if (caminho === "caminho3") {
            this.setState({ result: "M3",
                            grupo: DicionarioArvores.grupoM3});
          } else if (caminho === "caminho4") {
            this.setState({ result: "M4",
                            grupo: DicionarioArvores.grupoM4});
          } else if (caminho === "caminho5"||caminho === "caminho6"||caminho === "caminho7"||caminho === "caminho8") {
            this.setState({ result: "P",
                            grupo: DicionarioArvores.grupoP});
          }
        }
      } else if (respostas[0]==="larg3"){
        if (col === "c3"){
          if (caminho === "caminho1"||caminho === "caminho5") {
            this.setState({ result: "G4",
                            grupo: DicionarioArvores.grupoG4});
          } else if (caminho ===  "caminho2"||caminho === "caminho6") {
            this.setState({ result: "G5",
                            grupo: DicionarioArvores.grupoG5});
          } else if (caminho === "caminho3"||caminho === "caminho7") {
            this.setState({ result: "G6",
                            grupo: DicionarioArvores.grupoG6});
          } else if (caminho === "caminho4"||caminho === "caminho8") {
            this.setState({ result: "G7",
                            grupo: DicionarioArvores.grupoG7});
          }
        } else if (col === "c2"){
          if (caminho === "caminho1") {
            this.setState({ result: "M5",
                            grupo: DicionarioArvores.grupoM5});
          } else if (caminho === "caminho2") {
            this.setState({ result: "M6",
                            grupo: DicionarioArvores.grupoM6});
          } else if (caminho === "caminho3") {
            this.setState({ result: "M9",
                            grupo: DicionarioArvores.grupoM9});
          } else if (caminho === "caminho4") {
            this.setState({ result: "M11",
                            grupo: DicionarioArvores.grupoM11});
          } else if (caminho === "caminho5"||caminho === "caminho6"||caminho === "caminho7"||caminho === "caminho8") {
            this.setState({ result: "P",
                            grupo: DicionarioArvores.grupoP});
          }
        }
      } else if (respostas[0]==="larg4"){
        if (col === "c3"){
          if (caminho === "caminho1"||caminho === "caminho5") {
            this.setState({ result: "G8",
                            grupo: DicionarioArvores.grupoG8});
          } else if (caminho ===  "caminho2"||caminho === "caminho6") {
            this.setState({ result: "G9",
                            grupo: DicionarioArvores.grupoG9});
          } else if (caminho === "caminho3"||caminho === "caminho7") {
            this.setState({ result: "G14",
                            grupo: DicionarioArvores.grupoG14});
          } else if (caminho === "caminho4"||caminho === "caminho8") {
            this.setState({ result: "G10",
                            grupo: DicionarioArvores.grupoG10});
          }
        } else if (col === "c2"){
          if (caminho === "caminho1") {
            this.setState({ result: "M7",
                            grupo: DicionarioArvores.grupoM7});
          } else if (caminho === "caminho2") {
            this.setState({ result: "M8",
                            grupo: DicionarioArvores.grupoM8});
          } else if (caminho === "caminho3") {
            this.setState({ result: "M10",
                            grupo: DicionarioArvores.grupoM10});
          } else if (caminho === "caminho4") {
            this.setState({ result: "M12",
                            grupo: DicionarioArvores.grupoM12});
          } else if (caminho === "caminho5"||caminho === "caminho6"||caminho === "caminho7"||caminho === "caminho8") {
            this.setState({ result: "P",
                            grupo: DicionarioArvores.grupoP});
          }
        }
      } else if (respostas[0]==="larg5"){
        if (col === "c3"){
          if (caminho === "caminho1"||caminho === "caminho5") {
            this.setState({ result: "G11",
                            grupo: DicionarioArvores.grupoG11});
          } else if (caminho ===  "caminho2"||caminho === "caminho6") {
            this.setState({ result: "G12",
                            grupo: DicionarioArvores.grupoG12});
          } else if (caminho === "caminho3"||caminho === "caminho7") {
            this.setState({ result: "G15",
                            grupo: DicionarioArvores.grupoG15});
          } else if (caminho === "caminho4"||caminho === "caminho8") {
            this.setState({ result: "G13",
                            grupo: DicionarioArvores.grupoG13});
          }
        } else if (col === "c2"){
          if (caminho === "caminho1") {
            this.setState({ result: "M7",
                            grupo: DicionarioArvores.grupoM7});
          } else if (caminho === "caminho2") {
            this.setState({ result: "M8",
                            grupo: DicionarioArvores.grupoM8});
          } else if (caminho === "caminho3") {
            this.setState({ result: "M10",
                            grupo: DicionarioArvores.grupoM10});
          } else if (caminho === "caminho4") {
            this.setState({ result: "M12",
                            grupo: DicionarioArvores.grupoM12});
          } else if (caminho === "caminho5"||caminho === "caminho6"||caminho === "caminho7"||caminho === "caminho8") {
            this.setState({ result: "P",
                            grupo: DicionarioArvores.grupoP});
          }
        }
      }
  }

  renderQuiz() {
    return (
      <div>
      <Quiz
        answer={this.state.answer}
        answerOptions={this.state.answerOptions}
        questionId={this.state.questionId}
        question={this.state.question}
        questionInfo={this.state.questionInfo}
        questionTotal={perguntasQuiz.length}
        onAnswerSelected={this.handleAnswerSelected}
        handleBtnVoltar={this.handleBtnVoltar}
      />
      </div>
    );
  }

  renderResult() {
    return (
      <div>
        {this.state.grupo ? <Result quizResult={this.state.result} grupoArvore={this.state.grupo}/> : <ResultadoInapto />}
      </div>
    );
  }

  visitPage(){
      window.location='http://www.prefeitura.sp.gov.br/cidade/secretarias/meio_ambiente/publicacoes_svma/index.php?p=188452';
  }

  voltaPaginaIncial(){
   ReactDOM.render( <App />, document.getElementById('root'));
  }

  render() {
    return (
      <div>
      <div  className="headerQuiz" style={{backgroundImage: "url(/head.png)"}}>
        <img src="./title.png" className="logoapp" alt="Titulo"/>
        <img className="logosp" alt="LogoSP"/>
      </div>
        <div>
          {this.state.result ? this.renderResult() : this.renderQuiz()}
        </div>
      </div>
    );
  }


}

export default PaginaInicial;



