import React from 'react';

function Question(props) {

  function infos(questionInfo) {

    if (props.questionInfo !== '') {
      return (
        <h5 style={{fontWeight: 300}} className="questioninf">{props.questionInfo}</h5>
      );
    }
  }

    return (
      <div>
        <h4 style={{fontWeight: 300}} className="question">{props.content}</h4>
        {infos()}
      </div>
    );

}


export default Question;



