import React, { Component } from 'react';
import {Button} from 'react-materialize';
import {Link} from 'react-router-dom';

class PaginaInicial extends Component {
  visitPage(){
      window.location='http://www.prefeitura.sp.gov.br/cidade/secretarias/meio_ambiente/publicacoes_svma/index.php?p=188452';
  }

  render() {
    return (
      <div>
        <div  className="headerPrincipal">
          <img src="./title.png" className="logoapp" alt="Titulo"/>
          <img className="logosp" alt="LogoSP"/>
        </div>
        <div style={{backgroundImage: "url(./home.png)", width: '100%', height: '100%', textAlign: "center", paddingTop: "2.5%"}}>
          <h3 style={{textAlign: "center", color: "white", fontFamily: "Arial, Gadget, sans-serif"}}>Como funciona<br/>____</h3>
          <div className="flex-container">
            <div className="icone" style={{height: '300px', width: '320px'}}>
              <img src="./im1.png" alt="Icone1" width="190px" height="190px"/>
              <h5 style={{textAlign: "center", color: "white", lineHeight: "1", fontWeight: "bold"}}>Faça o teste</h5>
              <h5 style={{textAlign: "center", color: "white"}}>Veja se o local escolhido para o plantil está dentro da regulamentação com um teste rápido.</h5>
            </div>
            <div className="icone" style={{height: '300px', width: '320px'}}>
              <img src="./im2.png" alt="Icone2" width="190px" height="190px"/>
              <h5 style={{textAlign: "center", color: "white", lineHeight: "1", fontWeight: "bold"}}>Escolha o que plantar</h5>
              <h5 style={{textAlign: "center", color: "white"}}>Depois de finalizar o teste, uma lista com suas opções de plantas será exibida para que você possa escolher.</h5>
            </div>
            <div className="icone" style={{height: '300px', width: '320px'}}>
              <img src="./im3.png" alt="Icone3" width="190px" height="190px"/>
              <h5 style={{textAlign: "center", color: "white", lineHeight: "1", fontWeight: "bold"}}>Plante</h5>
              <h5 style={{textAlign: "center", color: "white"}}>Plante sem medo de ser feliz.</h5>
            </div>
            </div>
          <div className="flex-container" style={{marginBottom: "30px"}}>
            <Link to='/info'><Button large waves="teal" className="red" style={{borderRadius: 35, margin: '8px', width: '240px'}} >Começar o teste</Button></Link>
            <Button large waves="light" style={{borderRadius: 35, margin: '8px', width: '240px'}} onClick={this.visitPage}>Abrir o manual</Button>
            <Link to="/aprenda"><Button large waves="light" className="#7cb342 light-green darken-1" style={{borderRadius: 35, margin: '8px', width: '240px'}}>Aprenda a plantar</Button></Link>
            <Link to='/inadequadas'><Button large waves="light" className="#c0ca33 lime darken-2" style={{borderRadius: 35, margin: '8px', width: '240px'}}>Arvores inadequadas</Button></Link>
          </div>
      </div>
    </div>
  );}

}

export default PaginaInicial;



