import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Button, Icon} from 'react-materialize';
import {Link} from 'react-router-dom';

function ResultadoInapto(props) {

  return (
    <ReactCSSTransitionGroup
      className="containerr result"
      component="div"
      transitionName="fade"
      transitionEnterTimeout={800}
      transitionLeaveTimeout={500}
      transitionAppear
      transitionAppearTimeout={500}
    >
      <div>
        <h4 style={{fontWeight: 300}}><strong>Calçada inapta para plantio!</strong></h4>
        <h5 style={{fontWeight: 300}}>O formulario foi interrompido pois alguma de suas respostas torna sua calçada inapta para plantio.</h5>
      </div>
      <div>
        <h5 style={{fontWeight: 300}}>• Não é aconselhado o plantio de àrvores em calçadas menores do que <strong>1,90</strong></h5>
      </div>
      <div>
      <p><br/><strong>Clique em RECOMEÇAR para refazer o formulario.</strong></p>
        <Link to='/'><Button flat className="btn" style={{fixed:"left", padding: 0, marginTop: "10px"}}>Voltar<Icon left>arrow_back</Icon></Button></Link>
        <Link to='/info'><Button flat className="btn" style={{fixed:"right", float:"right", padding: 0, marginTop: "10px"}}>Recomeçar<Icon left>refresh</Icon></Button></Link>
      </div>

    </ReactCSSTransitionGroup>
  );

}

export default ResultadoInapto;



