import React, { Component } from 'react';
import {Button, Icon} from 'react-materialize';
import {Link} from 'react-router-dom';
import createBrowserHistory from 'history/createBrowserHistory';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
const customHistory = createBrowserHistory();

class PaginaInfo extends Component {

  render(){
    return (
      <div>
      <div  className="headerQuiz" style={{backgroundImage: "url('/head.png')"}}>
        <Link to="/"><img src="./title.png" className="logoapp" alt="Titulo"/></Link>
        <img className="logosp" alt="LogoSP"/>
      </div>
        <ReactCSSTransitionGroup
          className="containerr result"
          component="div"
          transitionName="fade"
          transitionEnterTimeout={800}
          transitionLeaveTimeout={500}
          transitionAppear
          transitionAppearTimeout={500}
        >
          <div className="Info">
            <div>
            <img src="./Logo64_Original.png" alt="LogoDAF-4" style={{display:"block", marginLeft:"auto", marginRight:"auto"}}/>
              <h5>Desenvolvido por DAF-4:</h5>
              <p className="text">Adauto Joaquim Ferreira Neves</p>
              <p className="text">Leonardo Carvalho Macarini </p>
              <p className="text">Leonardo Sampaio Francisco</p>
              <p className="text">Rafael Hans Sandalo</p>
              <h5> Contribuição: </h5>
              <p className="text">Leonardo Kazuhiko </p>
              <p className="text">Adriano Simonetti</p>
              <p className="text">Djalma Santos </p>


              <Button flat className="btn" style={{fixed:"left", marginBottom:"2%", marginTop:"1%"}} onClick={customHistory.goBack} >Voltar <Icon left>arrow_back</Icon></Button>
            </div>
          </div>
        </ReactCSSTransitionGroup>
      </div>
    );
  }
}
export default PaginaInfo;



