import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Collapsible, CollapsibleItem, Button, Icon} from 'react-materialize';
import {Link} from 'react-router-dom';

function Result(props) {

  return (
    <div>
      {props.quizResult==="inadequadas" &&
        <div  className="headerQuiz" style={{backgroundImage: "url(/head.png)"}}>
            <Link to="/"><img src="./title.png" className="logoapp" alt="Titulo"/></Link>
          <img className="logosp" alt="LogoSP"/>
        </div>
      }
      <ReactCSSTransitionGroup
        className="containerr result"
        component="div"
        transitionName="fade"
        transitionEnterTimeout={800}
        transitionLeaveTimeout={500}
        transitionAppear
        transitionAppearTimeout={500}
      >
        <div>
          {props.quizResult!=="inadequadas" &&
            <h5 style={{fontWeight: 300}}>Grupo de àrvores a ser plantado é <strong>{props.quizResult}</strong>!</h5>
          }
          {props.quizResult==="inadequadas" &&
            <h6 style={{fontWeight: 300}}>Grupo de àrvores <strong>{props.quizResult} </strong>para plantio.</h6>
          }
          Clique na àrvore desejada para expandir.
        </div>
          <div className="card-grid-view">
            <Collapsible accordion >
              {props.grupoArvore.map(function(card){
                return (
                 <CollapsibleItem key={card.id} className="panel" header={card.nomePop}>
                 <img src={card.imgSrc} alt={card.imgSrc} style={{borderRadius: 4, width:"100%"}}/>
                 <p className="title">Nome cientifico: {card.nomeCientifico} </p>
                 <p className="title">Nome popular: {card.nomePop}</p>
                 <p className="desc">{card.desc}</p>
                 </CollapsibleItem>
                )
              })}
            </Collapsible>
          </div>
          <div>
            <Link to ='/info'><Button flat className="btn" style={{fixed:"left", marginBottom:"2%", marginTop:"1%", padding: 0}} >Voltar<Icon left>arrow_back</Icon></Button></Link>
            {props.quizResult!=="inadequadas" &&
              <Link to ='/aprenda'><Button flat className="btn" style={{fixed:"right", float:"right", marginBottom:"2%", marginTop:"1%", padding: 0}}>Aprenda a plantar <Icon left>filter_vintage</Icon></Button></Link>
            }
          </div>
      </ReactCSSTransitionGroup>
    </div>
  );

}

export default Result;



