import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import App from './App.js';
import PaginaInfo from './components/PaginaInfo.js';
import Form from './components/Form.js';
import Result from './components/Result.js';
import AprendaPlantar from './components/AprendaPlantar.js';
import CustomFooter from './components/CustomFooter.js';
import PaginaCredito from './components/PaginaCredito.js';
import DicionarioArvores from './api/DicionarioArvores.js';

ReactDOM.render(
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    <Switch>
        <Route path='/' exact={true} component={App} />
        <Route path='/info' component={PaginaInfo} />
        <Route path='/aprenda' component={AprendaPlantar} />
        <Route path='/form' component={Form} />
        <Route path='/creditos' component={PaginaCredito} />
        <Route path='/inadequadas' render={(props) => <Result {...props} quizResult="inadequadas" grupoArvore={DicionarioArvores.inadequadas} />} />
    </Switch>
  </BrowserRouter>, document.getElementById('root'));
ReactDOM.render( <BrowserRouter basename={process.env.PUBLIC_URL}><Route path='/' component={CustomFooter} /></BrowserRouter>, document.getElementById('footer'));



